






































































import poster from './poster.vue'

export default {
  components: {
    poster
  },
  data() {
    return {
      taskLoading: false,
      taskLists: [],
      callLists: {
        total: 0,
        data: []
      },
      redpacketShow: false,
      taskRedId: 0,
      opening: false,
      opened: false,
      isClickRed: false,
      scrrollTop: 0,
      nowScrollTop: 0,
      money: 0.00,
      posterData: {}
    }
  },
  computed: {
    config() {
      return this.$store.state.config
    },
    constant() {
    	return this.$constant
    },
    userInfo() {
      return this.$store.state.userInfo
    },
    isRole() {
      return this.userInfo.role > 0
    },
    defaultAvatar() {
      return '../../static/images/avatar.png'
    }
  },
  onShow() {
    if (!this.isRole) {
      this.showTips()
    } else {
      this.updateTask()
      this.$http({
        url: this.$apis.waitforCall,
        method: 'GET',
        data: null,
        cb: (err, data) => {
          if (!err && data.success) {
            this.callLists = data.data
          }
        }
      })
    }
  },
  onHide() {
  	this.nowScrollTop = this.scrrollTop
  },
  methods: {
    showTips() {
      uni.showModal({
      	title: '提示',
        content: '您是临时用户，无权使用此功能，请先免费升级为普通会员继续使用。',
        cancelText: '知道了',
        confirmText: '去升级',
        success: res => {
        	if (res.confirm) {
            this.$navigateTo({
            	url: '/pages/mine/agent'
            })
          }
        }
      })
    },
    updateTask(cb) {
      if (!this.isRole) {
        this.showTips()
        return
      }
      if (this.taskLoading) return
      this.taskLoading = true
      this.taskLists = []
      this.$http({
        url: this.$apis.task,
        method: 'GET',
        data: {
          type: 4
        },
        cb: (err, data) => {
          if (!err && data.success) {
            this.taskLists = data.data
            this.isClickRed = false
            if (typeof cb === 'function') {
              cb()
            }
          } else {
            uni.showToast({
            	title: data ? data.message : '服务器繁忙，请稍后再试',
              icon: 'none',
            })
          }
          this.taskLoading = false
        }
      })
    },
    scroll(e) {
      this.scrrollTop = e.detail.scrollTop
    },
    showPacket(id) {
      if (!id) {
        uni.showToast({
        	title: '完成任务才能抽红包哟',
          icon: 'none'
        })
      } else {
        this.redpacketShow = true
        this.opening = false
        this.opened = false
        this.taskRedId = id
      }
    },
    openRedPacket() {
      if (this.opening || this.isClickRed) return
      this.opening = true
      this.$http({
        url: this.$apis.taskRedpacket,
        method: 'POST',
        data: {
          id: this.taskRedId
        },
        cb: (err, data) => {
          if (!err && data.success) {
            this.money = data.data
            this.isClickRed = true
            setTimeout(() => {
              this.opening = false
              this.opened = true
              this.$getInfo()
            }, 1000)
          } else {
            setTimeout(() => {
              uni.showToast({
              	title: '抽红包失败，请稍后再试',
                icon: 'none'
              })
              this.opening = false
              this.opened = false
            }, 1000)
          }
        }
      })
    },
    closeRedPop() {
      if (this.isClickRed) {
        this.updateTask(() => {
          setTimeout(() => {
            this.nowScrollTop = scrrollTop
          }, 16.67)
        })
      }
      this.redpacketShow = false
    },
    gotoCalled() {
      this.$navigateTo({
      	url: '/pages/invite/called'
      })
    },
    createPoster(text, uid) {
      if (!this.isRole) {
        this.showTips()
        return
      }
      let type = 8
      switch (text) {
        case 'subscribe':
          type = 1
          break
        case 'invite_new':
          type = 2
          break
        case 'apply':
          type = 3
          break
        case 'banker':
          type = 4
          break
        case 'banker_advanced':
          type = 4
          break
      }
      this.posterData = {
        show: true,
        type: type,
        uid: uid
      }
    },
    posterError() {
      uni.showToast({
      	title: '海报生成失败',
        icon: 'none'
      })
    },
    gotoTask() {
      if (this.config.module_control_task === this.constant.MODULE_STATE_OPEN) {
        this.$navigateTo({
        	url: '/pages/task/index'
        })
      } else {
        uni.showToast({
        	title: '任务活动暂未开放',
          icon: 'none'
        })
      }
    },
    gotoEarning() {
      this.$navigateTo({
      	url: '/pages/mine/earning?tab=1'
      })
    }
  }
}
